<template>
  <v-container fluid class="pa-0">
    <v-row no-gutters>
      <v-col
        class="full-height"
        :class="{ 'pr-10': $vuetify.breakpoint.smAndDown }"
        cols="12"
        md="9"
      >
        <youtube
          ref="youtube"
          id="player"
          :width="'100%'"
          :height="'100%'"
        ></youtube>
      </v-col>
      <v-col
        cols="12"
        sm="8"
        md="3"
        class="position-absolute"
        :class="{
          'slide-in': panelShow,
          'slide-out': !panelShow
        }"
      >
        <v-btn
          class="pa-0 elevation-0 panel-switch hidden-md-and-up"
          color="primary"
          @click.stop="panel = !panel"
        >
          <v-icon>
            {{ panel ? "mdi-chevron-right" : "mdi-chevron-left" }}
          </v-icon>
        </v-btn>
        <transition name="delay">
          <v-card
            v-if="panelShow"
            class="elevation-5 full-height position-absolute"
            :class="{ 'pl-10': $vuetify.breakpoint.smAndDown }"
          >
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn-toggle v-model="window" color="primary" dense group>
                <v-btn :value="0" text>
                  <v-icon>mdi-forum</v-icon>
                </v-btn>
                <v-btn :value="1" text>
                  <v-icon>mdi-account-multiple</v-icon>
                </v-btn>
                <v-btn :value="2" text>
                  <v-icon>mdi-playlist-play</v-icon>
                </v-btn>
                <v-btn :value="3" text>
                  <v-icon>mdi-cog</v-icon>
                </v-btn>
              </v-btn-toggle>
            </v-card-actions>
            <v-window class="content overflow-y-auto" v-model="window">
              <v-window-item>
                <v-card-title class="pt-0">
                  Chat
                </v-card-title>
                <v-container id="chat" class="chat-content overflow-y-auto">
                  <template v-for="(message, index) in messages">
                    <v-divider :key="`divider${index}`"></v-divider>
                    <Message
                      v-if="message.author"
                      :key="index"
                      :time="message.time"
                      :author="message.author"
                      :content="message.content"
                      :avatar="message.avatar"
                    ></Message>
                    <ServerMessage
                      v-else
                      :key="index"
                      :icon="message.icon"
                      :content="$t(message.content, message.args)"
                    ></ServerMessage>
                  </template>
                </v-container>
                <v-footer class="py-0">
                  <v-text-field
                    v-model="message"
                    v-on:keyup.enter="sendMessage"
                    placeholder="Message"
                  >
                    <template v-slot:append-outer>
                      <v-btn
                        color="primary"
                        class="mr-2"
                        icon
                        @mouseenter="emojiToggle(true)"
                        @mouseleave="emojiToggle(false)"
                      >
                        <v-container
                          :class="[emojis.hover ? 'zoom' : 'unzoom']"
                          fluid
                        >
                          <Emoji
                            :name="emojis.name"
                            :disabled="!emojis.hover"
                          ></Emoji>
                        </v-container>
                      </v-btn>
                      <v-btn color="primary" icon v-on:click="sendMessage">
                        <v-icon>mdi-send</v-icon>
                      </v-btn>
                    </template>
                  </v-text-field>
                </v-footer>
              </v-window-item>
              <v-window-item>
                <v-card-title class="pt-0">
                  <v-row>
                    <v-col cols="auto">
                      Members
                    </v-col>
                    <v-spacer></v-spacer>
                    <v-col cols="auto" class="text-right">
                      <v-btn icon @click="inviteDia = !inviteDia">
                        <v-icon>mdi-account-multiple-plus</v-icon>
                      </v-btn>
                      <v-dialog v-model="inviteDia" max-width="600">
                        <v-card>
                          <v-card-title class="headline">
                            <v-row>
                              <v-col>
                                Invite Link
                              </v-col>
                              <v-col class="text-right">
                                <v-btn icon @click="inviteDia = false">
                                  <v-icon>mdi-close</v-icon>
                                </v-btn>
                              </v-col>
                            </v-row>
                          </v-card-title>
                          <v-card-subtitle
                            >Do you feel lonely? Invite your beloved friends by
                            giving them this URL!
                          </v-card-subtitle>
                          <v-card-text>{{ inviteURL + roomID }} </v-card-text>
                          <v-card-actions>
                            <v-flex class="text-right">
                              <v-btn @click="copyLink">Copy</v-btn>
                            </v-flex>
                          </v-card-actions>
                        </v-card>
                      </v-dialog>
                    </v-col>
                  </v-row>
                </v-card-title>
                <v-card-subtitle class="pb-0"
                  >Here you can find the list of the members!
                </v-card-subtitle>
                <v-list class="overflow-y-auto" fixed>
                  <v-row justify="center" align="center" class="mr-0 ml-0">
                    <Member
                      v-for="(value, index) in members"
                      :key="index"
                      :nickname="value.name"
                      :avatar="value.avatar"
                      :role="value.owner ? 0 : value.dj ? 1 : 2"
                    ></Member>
                  </v-row>
                </v-list>
              </v-window-item>
              <v-window-item>
                <v-card-title class="pt-0">
                  <v-row>
                    <v-col>
                      Playlist
                    </v-col>
                    <v-col class="text-right">
                      <v-btn
                        v-if="this.me.owner"
                        @click="addVideoDia = !addVideoDia"
                        icon
                      >
                        <v-icon>mdi-playlist-plus</v-icon>
                      </v-btn>
                      <v-dialog v-model="addVideoDia" max-width="600">
                        <v-card>
                          <v-card-title class="headline">
                            <v-row>
                              <v-col>
                                Add a video
                              </v-col>
                              <v-col class="text-right">
                                <v-btn icon @click="addVideoDia = false">
                                  <v-icon>mdi-close</v-icon>
                                </v-btn>
                              </v-col>
                            </v-row>
                          </v-card-title>
                          <v-card-subtitle
                            >Do you have a favorite video? Get its URL and
                            submit it here!
                          </v-card-subtitle>
                          <v-card-text>
                            <v-text-field
                              :rules="rules"
                              :error-messages="videoDiaErrorMessages"
                              v-model="newVideo"
                              label="Video URL"
                              placeholder="https://www.youtube.com/watch?v=DODLEX4zzLQ"
                              outlined
                            ></v-text-field>
                          </v-card-text>
                          <v-card-actions>
                            <v-flex class="text-right">
                              <v-btn @click="addVideo">Submit</v-btn>
                            </v-flex>
                          </v-card-actions>
                        </v-card>
                      </v-dialog>
                    </v-col>
                  </v-row>
                </v-card-title>
                <v-card-subtitle
                  >Here is the queue of the videos!
                </v-card-subtitle>
                <Video
                  v-for="(value, index) in playlist"
                  :key="index"
                  :index="index"
                  :url="value.url"
                  :title="value.title"
                  :by="value.by"
                ></Video>
              </v-window-item>
              <v-window-item>
                <v-card-title class="pt-0">
                  <v-row>
                    <v-col>
                      Settings
                    </v-col>
                  </v-row>
                </v-card-title>
                <v-card-subtitle>You have the full control! </v-card-subtitle>
              </v-window-item>
            </v-window>
          </v-card>
        </transition>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import socketio from "socket.io-client";
import Member from "@/components/watch/Member.vue";
import ServerMessage from "@/components/watch/ServerMessage.vue";
import Message from "@/components/watch/Message.vue";
import Emoji from "@/components/watch/Emoji.vue";
import Video from "@/components/watch/Video";

let socket;
export default {
  name: "Watch",
  components: {
    Video,
    Message,
    ServerMessage,
    Member,
    Emoji
  },
  data: () => ({
    panel: true,
    window: 1,
    emojis: {
      hover: false,
      name: Emoji.random()
    },
    inviteDia: false,
    addVideoDia: false,
    checkbox: true,
    radioGroup: 1,
    switch1: true,
    rules: [
      value => !!value || "Required.",
      value => (value && value.length >= 25) || "Min 25 characters"
    ],
    videoIndex: -1,
    roomID: "",
    me: {},
    newVideo: "",
    members: [],
    inviteURL: "https://videam.org/watch?id=",
    messages: [],
    message: "",
    videoDiaErrorMessages: "",
    playlist: []
  }),
  computed: {
    player() {
      return this.$refs.youtube.player;
    },
    panelShow() {
      return this.panel || this.$vuetify.breakpoint.mdAndUp;
    }
  },
  destroyed() {
    if (socket) {
      socket.disconnect();
    }
  },

  mounted() {
    window.scrollTo({ top: 64, behavior: "smooth" });
    const VideoState = {
      ENDED: 0,
      PLAYING: 1,
      PAUSED: 2,
      CUED: 5
    };
    window.player = this.player;
    this.player.on("stateChange", async event => {
      if (socket.me.owner && event.data !== 3) {
        const time = await this.player.getCurrentTime();
        socket.emit("videoState", event.data, Date.now(), time);
      }
    });

    socket = socketio(process.env.VUE_APP_WS_URL);
    socket.emit("join", { roomID: this.$route.query.id });

    socket.on("disconnect", () => {
      socket.disconnect();
    });

    socket.on("videoState", async (state, date, time) => {
      const currTime = await this.player.getCurrentTime();
      if (currTime - time >= 2) {
        if (state !== VideoState.ENDED) this.player.seekTo(time);
      }
      switch (state) {
        case VideoState.PAUSED:
          this.player.pauseVideo();
          break;
        case VideoState.PLAYING:
          this.player.playVideo();
          break;
      }
    });

    socket.on("playVideo", index => {
      console.log("play", index);
      this.playVideo(index);
    });

    socket.on("err", err => {
      if (err.type === "cannotJoin") this.$router.push({ path: "404" });
    });

    socket.on("roomJoin", data => {
      socket.me = this.me = data.me;

      this.roomID = data.roomID;
      this.members = data.members;
      this.$router
        .push({ path: "watch", query: { id: data.roomID } })
        .catch(() => {});
      this.owners = data.owners;
    });

    socket.on("memberUpdate", data => {
      const member = this.members.findIndex(user => user.id === data.id);
      if (member !== -1) this.members[member] = data;
    });

    socket.on("userJoin", data => {
      if (!this.getMember(data.id)) this.members.push(data);
      this.messages.push({
        icon: "mdi-account-plus",
        content: "user_join",
        args: { name: data.name }
      });
    });
    socket.on("userLeave", (id, name) => {
      const member = this.removeMember(id);
      if (member)
        this.messages.push({
          icon: "mdi-account-minus",
          content: "user_left",
          args: { name }
        });
    });
    socket.on("roomMsg", data => {
      const lastMessage = this.messages[this.messages.length - 1];
      if (lastMessage && data.author === lastMessage.author)
        lastMessage.content.push(data.content);
      else {
        const date = new Date(data.time);
        console.log(data);
        this.messages.push({
          author: data.author,
          content: [data.content],
          avatar: data.avatar,
          time: `${date.getHours()}:${date.getMinutes()}`
        });
      }
    });
    socket.on("serverMsg", (msg, args, icon) => {
      this.messages.push({
        content: msg,
        args: args,
        icon: icon
      });
    });
    socket.on("addVideo", data => {
      if (this.getMember(data.by)) {
        if (this.addVideoDia) this.addVideoDia = false;
        this.playlist.push({
          url: "https://www.youtube.com/watch?v=" + data.id,
          ...data,
          by: this.getMember(data.by).name
        });
      }
    });
    socket.on("addVideoError", err => {
      this.videoDiaErrorMessages = this.i18n.$t(err);
    });
  },
  methods: {
    playVideo(index) {
      index = index >= 0 ? index : this.videoIndex;
      if (this.playlist.length > index) {
        this.player.loadVideoById(this.playlist[index].id);
        this.videoIndex = index;
      }
    },
    emojiToggle(hover) {
      this.emojis.hover = hover;
      if (hover) {
        this.emojis.name = Emoji.random();
      }
    },
    copyLink() {
      navigator.clipboard.writeText(this.inviteURL + this.roomID);
    },
    sendMessage() {
      if (!this.message) return;
      socket.emit("roomMsg", { content: this.message });
      this.message = "";
    },
    addVideo() {
      socket.emit("addVideo", this.newVideo);
    },
    getMember(id) {
      return this.members.find(user => user.id === id);
    },
    removeMember(id) {
      const userIndex = this.members.findIndex(user => user.id === id);
      if (userIndex !== -1) return this.members.splice(userIndex, 1);
    }
  },
  watch: {
    messages: {
      deep: true,
      handler() {
        this.$nextTick(() => {
          const chat = document.getElementById("chat");
          if (chat) chat.scrollTop = chat.scrollHeight;
        });
      }
    }
  }
};
</script>

<style>
#player {
  margin-bottom: -7px;
}
</style>
<style lang="sass" scoped>
$duration: 0.25s

.content
  height: calc(100vh - 60px)

.chat-content
  height: calc(100vh - 178px)

.full-height
  height: 100vh
  max-height: 100vh

.position-absolute
  position: absolute
  right: 0
  top: 0
  width: 100%

.delay-enter-active,
.delay-leave-active
  transition: display $duration

.slide-in
  transition: transform $duration ease-in

.slide-out
  transition: transform $duration ease-out
  transform: translate(calc(100% - 40px))

.panel-switch
  position: relative
  top: 0
  left: 0
  height: 100vh !important
  width: 40px
  min-width: 40px !important
  z-index: 1

.unzoom
  transition: transform $duration ease-out
  transform: scale(1)

.zoom
  transition: transform $duration ease-in
  transform: scale(1.2)
</style>
