<template>
  <v-img
    :src="twemoji"
    aspect-ratio="1"
    contain
    :width="size"
    :class="{ disabled: disabled }"
  ></v-img>
</template>

<script>
import emojis from "@/utils/emojis.json";

const random = () => {
  return Object.keys(emojis)[Math.floor(Math.random() * 93)];
};

export default {
  name: "Emoji",
  props: {
    name: {
      type: String,
      default: random
    },
    size: {
      default: "1.7em"
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    twemoji() {
      return `https://twemoji.maxcdn.com/v/latest/72x72/${
        emojis[this.name]
      }.png`;
    }
  },
  emojis,
  random
};
</script>

<style scoped>
.disabled {
  filter: grayscale(100%);
}
</style>
